body{
  font-family: roboto;
  background-color:#eae7dc;
  color:#474b4f;
}
a {color:inherit;}
h1{font-size:5em;}
p{font-size:1em;}
h3{padding-top:1em;}
.header a {text-decoration:none;}
.header{
  width:100%;
  display:flex;
  align-items:center;
  font-weight:bolder;
}
.header-name{
  flex-grow:3;
  font-size:2.5em;
  color:#e85a4f !important;
  text-decoration:none !important;
}
.header-item{
  flex-grow:1;
  font-size:1em;
  color:#e85a4f !important;
  text-decoration:none !important;
}
.homepage-content {
  display:flex;
  width: 100%;
  align-items:center;
}
img {
  width:90%;
  padding:0 5% 0 5%;   
}
.contact-widgets {
  display:flex;
  flex-flow:row;
}
.page-content {
  align-items:left;
}
.contact-widgets img {width:2em;}
.widget { padding: .5em .5em .5em 0px}
.article-image {
  display:flex;
  justify-content:center;
}
.article-image img { max-width:500px;}
@media screen and (max-width: 767px) {
  .header, .page-content {
      padding: 2.5% 0 2.5% 5%;
  }
  .homepage-content {
      flex-direction:column;
  }
  .bio-image {
      width:100%;
  }
  .bio-textbox {
      width:90%;
      padding:0 2.5% 0 2.5%;
  }
}   
@media screen and (min-width: 768px) {
  .header {
      padding: 2.5% 0 2.5% 2.5%;
  }
  .page-content {
    padding: 0 10% 0 10%;
  }
  .homepage-content {
      flex-direction:row;
  }  
  .bio-image {
      width:50%;
  }
  .bio-textbox {
      width:50%;
      padding:0 5% 0 5%;
  }
}